import styled from 'react-emotion';

const Button = styled('button')({
  fontSize: 24,
  backgroundColor: 'transparent',
  borderRadius: 4,
  padding: '18px 24px',
  margin: '0px 12px',
  boxShadow: '0px 2px 4px #ddd',
  cursor: 'pointer',
});

export default Button;



